import React from 'react';
import styled from 'styled-components';
import factory from './factory.png';
import recycle from './recycle-sign.png';
import { useHistory } from 'react-router';

const MainChoose = styled.div`
  width: 90vw;
  height: 100vh;
  background-color: #353b37;
  display: flex;
  justify-content: center;
  align-content: center;

  @media screen and (orientation: portrait) {
    width: 100vw;
    height: 92vh;
    display: flex;
    flex-direction: column;
  }
`;

const ChooseCard = styled.div`
  width: 45vw;
  height: 100vh;
  cursor: pointer;
  transition: 600ms;

  :hover {
    background-color: #248f41;
  }

  @media screen and (orientation: portrait) {
    width: 100vw;
    height: 46vh;
  }
`;

const ChooseImage = styled.div`
  width: 25vw;
  height: 25vw;
  display: block;
  background-size: cover;
  background-image: url(${(props) => props.imgSrc});
  align-self: center;
  margin: auto auto;
  margin-top: 10vh;
`;

const ChooseTitle = styled.h1`
  text-align: center;
  color: white;
  font-size: 5vw;

  @media screen and (orientation: portrait) {
    font-size: 5vh;
  }
`;

const Choose = () => {
  const history = useHistory();
  return (
    <MainChoose>
      <ChooseCard onClick={() => history.push('/eco/app/emission')}>
        <ChooseImage imgSrc={factory} />
        <ChooseTitle>Emisja CO2</ChooseTitle>
      </ChooseCard>
      <ChooseCard onClick={() => history.push('/eco/app/recycle')}>
        <ChooseImage imgSrc={recycle} />
        <ChooseTitle>Segregacja</ChooseTitle>
      </ChooseCard>
    </MainChoose>
  );
};

export default Choose;
