import React from 'react';
import styled from 'styled-components';

const MainRecycle = styled.div`
  width: 90vw;
  height: 100vh;
  background-color: #353b37;

  @media screen and (orientation: portrait) {
    width: 100vw;
    height: 92vh;
  }
`;

const Recycle = () => {
  return <MainRecycle></MainRecycle>;
};

export default Recycle;
