import React from 'react';
import logo from './logo.svg';
import './App.css';
import Home from './components/Home';
import About from './components/About';
import Navbar from './components/Navbar';
import Choose from './components/Choose';
import Emission from './components/Emission';
import Recycle from './components/Recycle';
import styled from 'styled-components';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

const MainApp = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;

  @media screen and (orientation: portrait) {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
`;

function App() {
  const [landscape, setLandscape] = React.useState(true);
  React.useEffect(() => {
    setInterval(() => {
      if (window.innerWidth >= window.innerHeight) {
        setLandscape(true);
      } else {
        setLandscape(false);
      }
    }, 1000);
  }, []);
  return (
    <MainApp>
      <BrowserRouter>
        {landscape === true ? (
          <>
            <Navbar />
            <Switch>
              <Route exact path='/eco/' component={Home} />
              <Route exact path='/eco/app' component={Choose} />
              <Route exact path='/eco/about' component={About} />
              <Route exact path='/eco/app/emission' component={Emission} />
              <Route exact path='/eco/app/recycle' component={Recycle} />
            </Switch>
          </>
        ) : (
          <>
            <Switch>
              <Route exact path='/eco/' component={Home} />
              <Route exact path='/eco/app' component={Choose} />
              <Route exact path='/eco/about' component={About} />
              <Route exact path='/eco/app/emission' component={Emission} />
              <Route exact path='/eco/app/recycle' component={Recycle} />
            </Switch>
            <Navbar />
          </>
        )}
      </BrowserRouter>
    </MainApp>
  );
}

export default App;
