import React from 'react';
import styled from 'styled-components';
import earth from './planet-earth.png';
import Button from '@mui/material/Button';
import { useHistory } from 'react-router-dom';

const MainHome = styled.div`
  width: 90vw;
  height: 100vh;
  background-color: #353b37;

  @media screen and (orientation: portrait) {
    width: 100vw;
    height: 92vh;
  }

  button {
    background-color: #32a852;
    width: 15vw;
    height: 4vw;
    font-size: 1vw;
    display: block;
    margin-left: 37.5vw;
    text-decoration: none;

    @media screen and (orientation: portrait) {
      width: 45vw;
      height: 12vw;
      margin-left: 27.5vw;
      font-size: 2vh;
    }
  }
  button:hover {
    background-color: #187a33;
  }
`;

const Title = styled.h1`
  font-size: 10vw;
  color: white;
  align-self: center;

  @media screen and (orientation: portrait) {
    font-size: 11vh;
    align-self: center;
  }
`;

const Logo = styled.div`
  width: 20vw;
  height: 20vw;
  background-image: url(${earth});
  background-size: cover;
  display: block;

  @media screen and (orientation: portrait) {
    width: 45vw;
    height: 45vw;
    align-self: center;
  }
`;

const Header = styled.div`
  width: 40vw;
  height: 50vh;
  display: flex;
  margin-left: 25vw;
  margin-top: 17vh;
  justify-content: center;

  @media screen and (orientation: portrait) {
    width: 90vw;
    margin-left: 5vw;
  }
`;

const Home = () => {
  const history = useHistory();
  return (
    <MainHome>
      <Header>
        <Logo />
        <Title>ECO</Title>
      </Header>
      <Button onClick={() => history.push('/eco/app')} variant='contained'>
        Przejdź Dalej
      </Button>
    </MainHome>
  );
};

export default Home;
