import React from 'react';
import styled from 'styled-components';
import Button from '@mui/material/Button';

const MainEmission = styled.div`
  width: 90vw;
  height: 100vh;
  background-color: #353b37;

  h1 {
    text-align: center;
    width: 40vw;
    color: white;
    font-size: 2.4vw;
    margin: auto auto;
  }

  @media screen and (orientation: portrait) {
    width: 100vw;
    height: 92vh;
    h1 {
      width: 80vw;
      font-size: 3.2vh;
      transform: translateY(10vh);
    }
  }
`;

const Form = styled.form`
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  width: 80vw;
  height: 40vh;
  justify-items: center;
  align-items: center;
  margin-left: 5vw;

  @media screen and (orientation: portrait) {
    height: 30vh;
    margin-left: 10vw;
  }

  button {
    background-color: #32a852;
    width: 15vw;
    height: 4vw;
    font-size: 0.8vw;
    display: block;
    text-decoration: none;

    :hover {
      background-color: #187a33;
    }

    @media screen and (orientation: portrait) {
      width: 34vw;
      height: 5vh;
      font-size: 1.2vh;
    }
  }
`;
const CoordInput = styled.input`
  width: 36vw;
  height: 6vh;
  font-size: 3.5vw;
  border-radius: 45px;
  text-align: center;
`;

const Emission = () => {
  const apiKey = '70411cb61b8f152baafc9f050ea72725';
  const [values, setValues] = React.useState([]);

  const i1 = React.useRef();
  const i2 = React.useRef();

  const handleGetLocation = async () => {
    await navigator.geolocation.getCurrentPosition(async (position) => {
      i1.current.value = position.coords.latitude;
      i2.current.value = position.coords.longitude;
    });
  };

  const handleCheckEmission = (e) => {
    e.preventDefault();
    fetch(
      `http://api.openweathermap.org/data/2.5/air_pollution?lat=${i1.current.value}&lon=${i2.current.value}&appid=${apiKey}`
    )
      .then((res) => res.json())
      .then((data) => {
        let arr = [...values];
        arr[0] = data.list[0].components.co;
        arr[1] = data.list[0].components.nh3;
        arr[2] = data.list[0].components.no;
        arr[3] = data.list[0].components.no2;
        arr[4] = data.list[0].components.o3;
        arr[5] = data.list[0].components.pm2_5;
        arr[6] = data.list[0].components.pm10;
        arr[7] = data.list[0].components.so2;
        setValues(arr);
        console.log(data);
        return data;
      });
  };
  return (
    <MainEmission>
      <Form onSubmit={handleCheckEmission}>
        <CoordInput ref={i1} />
        <CoordInput ref={i2} />
        <Button variant='contained' type='submit'>
          Sprawdź
        </Button>
        <Button variant='contained' type='button' onClick={handleGetLocation}>
          Pobierz swoją lokalizację
        </Button>
      </Form>
      <h1>Tlenek Węgla : {values[0]} μg/m3</h1>
      <h1>Amoniak : {values[1]} μg/m3</h1>
      <h1>Tlenek Azotu : {values[2]} μg/m3</h1>
      <h1>Dwutlenek Azotu : {values[3]} μg/m3</h1>
      <h1>Ozon : {values[4]} μg/m3</h1>
      <h1>PM2,5 : {values[5]} μg/m3</h1>
      <h1>PM10 : {values[6]} μg/m3</h1>
      <h1>Dwutlenek Siarki : {values[7]} μg/m3</h1>
    </MainEmission>
  );
};

export default Emission;
