import React from 'react';
import styled from 'styled-components';

const MainHome = styled.div`
  width: 90vw;
  height: 100vh;
  background-color: #353b37;

  @media screen and (orientation: portrait) {
    width: 100vw;
    height: 92vh;
  }

  h1 {
    text-align: center;
    color: white;
    font-size: 5vw;
    align-self: center;
    margin-top: 25vh;

    @media screen and (orientation: portrait) {
      font-size: 5vh;
    }
  }
`;

const About = () => {
  return (
    <MainHome>
      <h1>
        Aplikacja została stworzona przez Krystiana Zieję na I edycję
        Mini-Hackatonu
      </h1>
    </MainHome>
  );
};

export default About;
